html, body {
    font-family: Arial, Helvetica, sans-serif;
}

div #sorry {
    font-size: 30px;
}

h2 {
    margin: 20px;
}

img {
    width: 250px;
    height: 250px;
    border-color: none;
    border-radius: 20px;
}

a {
    text-decoration: none;
    color: #000010;
}

a:hover {
    opacity: 0.5;
}

h3 {
    max-width: 250px;
}

.flex-center {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
    margin: 0 auto;
}

.flex-row {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: row;
        flex-direction: row;
    margin: 0 auto;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.shadow-5 {
    -webkit-box-shadow: 4px 4px 8px 0px rgba( 0, 0, 0, 0.2);
            box-shadow: 4px 4px 8px 0px rgba( 0, 0, 0, 0.2);
}

:focus {
    outline: none;
}

h4 {
    font-size: 14px;
}

.games-searched {
    border-radius: 20px;
    padding: 20px;
    margin: 10px;
    background: #DAE2F8;
    /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #D6A4A4, #DAE2F8);
    /* Chrome 10-25, Safari 5.1-6 */
    background: -webkit-gradient(linear, left top, right top, from(#D6A4A4), to(#DAE2F8));
    background: -webkit-linear-gradient(left, #D6A4A4, #DAE2F8);
    background: -o-linear-gradient(left, #D6A4A4, #DAE2F8);
    background: linear-gradient(to right, #D6A4A4, #DAE2F8);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    -webkit-transition: 2000ms;
    -o-transition: 2000ms;
    transition: 2000ms;
    opacity: 0.95;
}

.games-searched:hover {
    opacity: 0.90;
    -webkit-transform: scale(1.01);
        -ms-transform: scale(1.01);
            transform: scale(1.01)
}

.card-text {
    margin-top: 15px;
}


