html, body {
    font-family: Arial, Helvetica, sans-serif;
}

#search {
    padding: 0.2rem;
    width: 25rem;
}

div#search-and-button {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex: wrap 1;
        flex: wrap 1;
}

input {
    border: none;
    border-radius: 20px;
    background-color: #80ced6;
    height: 60px;
    font-size: 20px;
    margin: 15px;
    opacity: 0.85;
}

@media screen and (max-width: 600px) {
    input {
        max-width: 200px;
        font-size: 15px;
    }
}

button {
    height: 60px;
    padding: 20px;
    cursor: pointer;
    border: none;
    border-radius: 20px;
    background-color: #80ced6;
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
    margin: 15px;
    opacity: 0.85;
}

button:hover {
    -webkit-transform: scale(1.2);
        -ms-transform: scale(1.2);
            transform: scale(1.2);
}

.shadow-5 {
    -webkit-box-shadow: 4px 4px 8px 0px rgba( 0, 0, 0, 0.2);
            box-shadow: 4px 4px 8px 0px rgba( 0, 0, 0, 0.2);
}


